<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-text="返回" left-arrow @click-left="goPerson" />

    <van-tabs v-model="$store.state.activeName" @click="changeItem">
      <van-tab
        :title="item.title"
        :name="item.name"
        v-for="(item, index) in tabContent"
        :key="index"
      >
        <div class="content">
          <div class="order-item" v-for="(item,index) in orderID" :key="index">
            <div class="item-title clearfix">
              <div class="id fl">
                <span class="order-num">{{item}}</span>
              </div>

              <div class="confirm" v-if="orderData[item].status==2">
                <van-button
                  type="danger"
                  round
                  size="mini"
                  class="fr button"
                  @click="removeOrder(item)"
                >
                  <van-icon name="delete" />
                </van-button>
                <van-button type="info" round size="mini" class="fr button">已收货</van-button>
              </div>
              <div v-else class="finish">
                <!-- <van-button type="danger" round size="mini" class="fr button" @click="removeOrder(item)"> <van-icon name="delete" /></van-button> -->
                <van-button
                  type="info"
                  round
                  size="mini"
                  class="fr button"
                  @click="confirmReceive(item)"
                >确认收货</van-button>
              </div>
            </div>

            <div class="item-content clearfix" v-for="(v,i) in orderData[item].data" :key="i">
              <div class="clearfix">
                <div class="item-img fl">
                  <img :src="v.smallImg" alt class="img_auto" />
                </div>

                <div class="item-text fl">
                  <div class="top">
                    <div class="name">
                      <p>{{v.name}}</p>
                    </div>
                    <div class="rule">
                      <p>{{v.rule}}</p>
                    </div>
                  </div>
                  <div class="price">
                    <span class="fl span1">{{v.price}}</span>
                    <span class="fr">X{{v.count}}</span>
                  </div>
                  <div >
                  <span class="fl">{{orderData[item].date | formatDate('yyyy-MM-dd hh:mm:ss')}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tabContent: [
        {
          title: "全部",
          name: "0"
        },
        {
          title: "待收货",
          name: "1"
        },
        {
          title: "已收货",
          name: "2"
        }
      ],
      orderData: [],
      //订单编号
      orderID: ""
    };
  },
  methods: {
    asess(item) {
      
      // this.$router.push({name:"评价",query:{
      //   pid:v.pid
      // }})
    },
    changeItem(a) {
      
      this.findOrder();
    },
    goPerson() {
      this.$router.push({ name: "个人" });
    },
    findOrder() {
      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.$store.state.activeName
        }
      })
        .then(res => {
          
          this.$toast.clear();
          if (res.data.code == 70000) {

             res.data.result.sort((a, b) => {
            return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
          })
            //定义获取所有订单号
            let orders = [];
            res.data.result.forEach(v => {
              if (orders.indexOf(v.oid) == -1) {
                orders.push(v.oid);
              }
              // 
            });
            this.orderID = orders;
            
            let orderlist = {};
            res.data.result.forEach(v => {
              //订单列表中没有相应的oid
              if (!orderlist[v.oid]) {
                orderlist[v.oid] = {
                  data: [v],
                  status: v.status,
                  date: v.createdAt,
                  count: v.count,
                  total: v.count * v.price
                };
                
              } else {
                orderlist[v.oid].count += v.count;
                orderlist[v.oid].total += v.count * v.price;
                orderlist[v.oid].data.push(v);
              }
            });
            this.orderData = orderlist;
            
          }
        })
        .catch(err => {
          
        });
    },

    //确认收货
    confirmReceive(oid) {
      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(res => {
          
          this.$toast.clear();
          if (res.data.code == 80000) {
            this.orderData[oid].status = 2;
            //待收货
            if (this.$store.state.activeName == 1) {
              let index = this.orderID.indexOf(oid);
              this.orderID.splice(index, 1);
            }
          }
          
        })
        .catch(err => {
          cosnoel.log(err);
        });
    },
    removeOrder(oid) {
      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 90000) {
            let index = this.orderID.indexOf(oid);
            this.orderID.splice(index, 1);
          }
          
        })
        .catch(err => {
          
        });
    }
  },
  created() {
    this.findOrder();
  }
};
</script>

<style lang="less" scoped>
.van-nav-bar {
  position: fixed;
  top: 0;
  width: 100%;
}
/deep/ .van-tabs--line .van-tabs__wrap {
  height: 1.17333rem;
  position: fixed;
  top: 35px;
  width: 100%;
  z-index: 10000000000;
}
/deep/ .van-tab--active {
  color: #6389fb;
}
.order {
  padding-bottom: 25px;
  .content {
    width: 90%;
    margin: 0 auto;
    margin-top: 95px;
    .order-item {
      width: 100%;
      border-radius: 15px;
      box-shadow: 3px 4px 5px 6px #f2f2f2;
      margin-top: 25px;
      padding: 10px 0;
      .item-content {
        // margin-top: 15x;
        width: 90%;
        margin: 10px auto;
        padding-right: 10px;
      }
      .item-title {
        height: 30px;
        line-height: 30px;
        width: 85%;
        margin: 0 auto;
        .button {
          margin-top: 4px;
          margin-left: 15px;
        }
      }
      .item-img {
        width: 35%;
        img {
          width: 75%;
          margin: 0 auto;
          border-radius: 15px;
        }
      }
      .item-text {
        width: 60%;
        margin-left: 15px;
        .top {
          .name {
            height: 30px;
            line-height: 30px;
          }
          .rule {
            height: 30px;
            line-height: 30px;
          }
        }
        p {
          margin: 0;
          padding: 0;
        }
      }
      .price {
        height: 30px;
        width: 80%;
        margin-top: 10px;
        .span1 {
          font-size: 18px;
          color: red;
        }
      }
    }
  }
}
/deep/ .van-button--mini {
  min-width: 60px;
  height: 0.58667rem;
  font-size: 0.26667rem;
}
</style>